<template>
  <div>
    <el-container>
      <el-header> <h3 style="margin-left: 0%">欢迎登录jj博客</h3> </el-header>
      <el-main>
        用户名：<el-input
          class="myInput"
          v-model="username"
          placeholder="请输入内容"
        ></el-input>
        <br />
        密码：<el-input
          class="myInput"
          placeholder="请输入密码"
          v-model="password"
          show-password
        ></el-input>
        <span style="color: red">{{ loginMsg }}</span>
        <br />

        <el-button type="primary" @click="loginMethod">登录</el-button>
        <el-button type="primary" @click="dialogVisible = true">注册</el-button>

        <el-dialog title="用户注册" :visible.sync="dialogVisible" width="30%">
          <el-form :model="form">
            <el-form-item label="账号" label-width="80px">
              <el-input v-model="form.username" autocomplete="off"></el-input>
              <span style="color:red">{{hasSame}}</span>
            </el-form-item>
            <el-form-item label="密码" label-width="80px">
              <el-input v-model="form.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" label-width="80px">
              <el-input v-model="form.email" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <span style="color: red">{{ loginMsg }}</span>

          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="register">确 定</el-button>
          </div>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "login",
  components: {
    axios,
  },
  data() {
    return {
      username: "",
      password: "",
      loginMsg: "",
      dialogVisible: false,
      form: {
        username: "",
        email: "",
        password: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      formLabelWidth: "120px",
      hasSame:''
    };
  },
  methods: {
    loginMethod(){
      var loginData = new FormData();
      loginData.append("username", this.username);
      loginData.append("password", this.password);
      
      this.$axs
        .post("http://192.168.126.129:8878/pro/user/login", loginData)
        .then((val) => {
          console.log(val)
          if (val.msg === "登录成功") {
            localStorage.setItem("token", val.data.token)
            this.$store.commit("userDetail/storageUser",val.data)
            localStorage.setItem("user", JSON.stringify( val.data))
            setTimeout(()=>{
              window.location.href = 'http://192.168.126.129:8080/#/blogList'
            },1000)
 
          }else{
            this.loginMsg = "账号密码错误";
          }
        })
        

    },

    register(){
      var loginData = new FormData();
      loginData.append("username", this.form.username);
      loginData.append("password", this.form.password);
      loginData.append("email", this.form.email);

      this.$axs
        .post("http://192.168.126.129:8878/pro/user/register", loginData)
        .then((val) => {
          if (val.code === 200) {
            alert("注册成功")
            this.dialogVisible = false
            localStorage.setItem("token",val.data)
            this.$store.commit("blogToken/storageToken",val.data)
          }else if(val.msg === '已存在的用户名'){
            this.hasSame = '已存在的用户名'
          }
        })
        
    },
  },
  mounted(){
    if(localStorage.getItem("token") != ""){
      this.$router.push('/blogList')
    }
  }
};
</script>

<style scoped>
.el-container {
  background-color: #fff !important ;
}

.el-header {
  background-color: rgb(231, 229, 229);
  font-weight: 500;
  width: 70%;
  margin-left: 15%;
}

.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
  width: 70%;
  margin-left: 15%;
}

.el-main {
  background-color: #e9eef3;
  color: rgb(211, 209, 209);
  text-align: center;
  line-height: 160px;
  width: 70%;
  margin-left: 15%;
}

.myInput {
  width: 20% !important;
  line-height: 40px;
}
</style>