<template>
<div >
  <el-container>
    <i  class="el-icon-s-home" style="cursor: pointer;color:ff8899" @click="goBack"></i>
    &ensp;&ensp;&ensp;&ensp;
    <span style="margin-top:3px;cursor:pointer;color:#aabbcc" @click="$router.push('/userSort')">用户排行榜</span>
    &ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;
    &ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;
    &ensp;&ensp;&ensp;&ensp;&ensp;
    <span style="margin-top:3px">{{title}}</span>
    &ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;
    <input v-model="searchInfo" type="text" style="width:60px;height:30px;margin-top:3px">
    <el-button @click="search()" size="small" style="height:40px;" icon="el-icon-search" circle></el-button>


    <el-popover
      placement="top-start"
      width="200"
      trigger="hover"
      style="margin-left: 20%; text-align: center"
    >
      <el-avatar :src="avatarImg" @click="goto" slot="reference"> </el-avatar>
      <h4>{{ username }}</h4>
      <img src="" alt="" />

      
      <el-divider></el-divider>
      <el-button
        style="width: 100%; background-color: pink; color: black"
        type="primary"
        @click="goto"
        >个人中心</el-button
      ><br />
      <el-button
        style="width: 100%; background-color: #ffccaa; color: black"
        type="primary"
        @click="$router.push('/blogedit')"
        >写文章</el-button>
      <br />
      <el-button
        style="width: 100%; background-color: #556677; color: black"
        type="primary"
        @click="showCollecton(userId)"
        >收藏夹</el-button>

        <span style="color:#218126;cursor:pointer" @click="signOut">退出登录</span>
    </el-popover>
  </el-container>

  <el-dialog
      title="收藏夹"
      :visible.sync="showDialog"
      width="70%"
      :before-close="handleClose"
    >
      <List style="margin-left:-20%" :fullcontent="myCol"></List>
    </el-dialog>

  
</div>
  
  
</template>

<script>
import List from './List.vue'
export default {
  name: "Header",
  components:{List},
  data() {
    return {
      fits: ["fill", "contain", "cover", "none", "scale-down"],
      avatarImg: JSON.parse(localStorage.getItem("user")).avatar,
      username: JSON.parse(localStorage.getItem("user")).username,
      userId: JSON.parse(localStorage.getItem("user")).id,
      searchInfo:'',
      showDialog:false,
      myCol:''
    };
  },
  props:['title'],
  methods: {
    signOut(){
      localStorage.clear()
      this.$router.push('/')
    },
    showCollecton(id){
      let formdata = new FormData()
      formdata.append("userId",id)
      this.$axs.post("http://192.168.126.129:8878/pro/collection/getMyCol",formdata)
        .then((val)=>{
          if(val.code === 303){
            alert("您没有收藏的文章")
          }else if(val.code === 401){
            localStorage.removeItem('token')
            this.$router.push("/")
          }else{
            this.myCol = val.data
            this.showDialog = true
          }
          
        })
    },
    search(){
      console.log(this.$route.path)
      let formdata = new FormData()
      formdata.append("searchInfo",this.searchInfo)
      if(this.$route.path === '/blogList'){
        this.$axs.post("http://192.168.126.129:8878/pro/blog/search",formdata)
          .then((val)=>{
            if(val.code === 401){
            this.$router.push("/")
          }else{
            this.$emit("searchInfo",val)
          }
            
          })
      }else if(this.$route.path === '/userSort'){
        this.$axs.post("http://192.168.126.129:8878/pro/user/search",formdata)
          .then((val)=>{
            if(val.code === 401){
              this.$router.push("/")
            }else{
              this.$emit("searchInfo",val)
          }
        })
      }
      
    },
    goBack() {
      this.$router.push("/");
    },
    goto() {
      if (
        localStorage.getItem("token") == null ||
        localStorage.getItem("token") == ""
      ) {
        this.$router.push("/login");
      } else {
        this.$router.push({
          path: "/myDetail",
          query: {
            curUserName:JSON.parse(localStorage.getItem("user")).username
          },
        });
      }
    },
    
  },
  mounted() {
    window.addEventListener("setItem", (e) => {
      this.avatarImg = JSON.parse(e.newValue).avatar;
    });
  },
};
</script>

<style scope>
.el-container {
  background-color: rgb(233, 232, 232);
  width: 95%;
  margin-left: 3%;
  position: relative;
  bottom: 7px;
}

h3 {
  text-align: center;
  margin-top: 5px;
  margin-left: 37%;
}

h4 {
  margin-top: 0px;
  text-align: center;
}

i {
  margin-top: 5px;
  width: 20px;
}
</style>